import { Component,ViewContainerRef } from '@angular/core';
import {LoadToComponentService} from './load-to-component.service'

@Component({
  selector: 'my-app',
  providers: [LoadToComponentService,ViewContainerRef],
  template: ` 
    <button (click)="create()">Load and create a component</button>
  `, 
  
})
export class NeedLoadComponentComponent { 
  constructor(public _s: LoadToComponentService) {}
  
  create() {
    this._s.open();
  }
}